@import url(https://fonts.googleapis.com/css2?family=Acme&family=Akaya+Telivigala&family=Fuzzy+Bubbles:wght@400;700&family=Luckiest+Guy&family=Poppins&family=Nunito&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.loading {
  align-items: center;
  display: flex;
  justify-content: center;
}

#loading-container{display:flex;justify-content:center;align-self:center;overflow:hidden}#loading-container img{border-radius:9999px}.page #loading-container{position:fixed;z-index:9999;width:100%;height:100vh;background-color:var(--color-background-gray-2);top:0;left:0}.page #loading-container img{width:60px;height:60px;margin:auto}
#login-page{display:flex;justify-content:center;align-items:center;min-height:100vh;background:linear-gradient(to bottom, var(--color-background-gray-1), var(--color-background-black))}#login-page #login-section{width:600px;background:linear-gradient(to bottom, var(--color-background-black), var(--color-background-gray-2));padding:40px 20px;border-radius:8px;display:flex;flex-direction:column;align-items:center;grid-gap:48px;gap:48px}#login-page #login-section #login-header{display:flex;flex-direction:column;align-items:center;grid-gap:34px;gap:34px}#login-page #login-section #login-header #page-icon-container{width:50px;height:50px;background-color:var(--color-background-white);border-radius:9999px;display:flex;justify-content:center;align-items:center}#login-page #login-section #login-header #page-icon-container #page-icon{width:80%}#login-page #login-section #login-header #login-title{color:var(--color-font-white);font-family:var(--font-family-title);font-weight:bolder;font-size:24px}#login-page #login-section #login-form{display:flex;flex-direction:column;width:60%;align-items:center;color:var(--color-font-white);grid-gap:28px;gap:28px}#login-page #login-section #login-form label{width:100%;display:flex;flex-direction:column;grid-gap:12px;gap:12px}#login-page #login-section #login-form label span{font-family:var(--font-family-label);font-size:16px}#login-page #login-section #login-form label input{width:100%;padding:8px 16px;outline:none;border-radius:2px;border:1px solid var(--color-border-gray-1);background-color:rgba(0,0,0,0);color:var(--color-font-white);font-family:var(--font-family-input);font-size:18px;transition:1s}#login-page #login-section #login-form label input:focus{box-shadow:0 0 0 1px var(--color-shadow-white)}#login-page #login-section #login-form button{width:100%;padding:12px;border-radius:9999px;border:none;background-color:var(--color-background-green-1);font-family:var(--font-family-button);font-size:18px;transition:1s}#login-page #login-section #login-form button:hover{cursor:pointer;filter:brightness(0.6)}#login-page #login-section #login-form button:disabled{filter:brightness(0.2)}
.album-card{text-decoration:none;display:flex;flex-direction:column;grid-gap:8px;gap:8px;padding:8px;border-radius:4px}.album-card .album-image{width:100%;border-radius:4px}.album-card .album-name,.album-card .artist-name{color:var(--color-font-white);font-family:var(--font-family-text)}.album-card .album-name{overflow:hidden;text-wrap:nowrap;text-overflow:ellipsis;font-size:18px}.album-card .artist-name{font-size:16px;filter:brightness(0.5)}.album-card:hover{background-color:var(--color-background-gray-1)}
#album-list-container{width:100%;display:flex;flex-direction:column;align-items:center;grid-gap:18px;gap:18px}#album-list-container #album-list-search{color:var(--color-font-white);font-family:var(--font-family-text);font-size:20px;font-weight:bold}#album-list-container #album-list-content{width:100%;display:flex;flex-wrap:wrap;justify-content:center;grid-gap:18px;gap:18px}#album-list-container #album-list-content .album-card{width:14%}
#search-page{background-color:var(--color-background-gray-3);flex-grow:1;border-radius:4px;padding:28px 8px;display:flex;flex-direction:column;grid-gap:24px;gap:24px}#search-page #search-page-form{display:flex;justify-content:center;grid-gap:18px;gap:18px}#search-page #search-page-form input{width:500px;outline:none;padding:8px;border:1px solid var(--color-background-gray-1);background-color:var(--color-background-gray-2);border-radius:2px;color:var(--color-background-white);font-family:var(--font-family-input);font-size:24px;transition:.5s}#search-page #search-page-form input:focus{box-shadow:0 0 0 1px var(--color-shadow-white)}#search-page #search-page-form button{padding:8px 16px;border-radius:2px;border:none;background-color:var(--color-background-green-1);font-family:var(--font-family-button);font-size:18px;transition:1s}#search-page #search-page-form button:hover:enabled{cursor:pointer}#search-page #search-page-form button:disabled{filter:brightness(0.5)}#search-page #album-list-section{display:flex;justify-content:center}#search-page #album-list-section #no-album-message{color:var(--color-font-white);font-family:var(--font-family-text);font-size:28px;font-weight:bold}
.music-card-container{display:flex;justify-content:space-between;align-items:center;background-color:var(--color-background-gray-1);padding:8px 18px;border-radius:4px;width:100%;max-width:800px;grid-gap:16px;gap:16px}.music-card-container .album-music-image{width:80px;border-radius:9999px}.music-card-container .audio-info-container{display:flex;flex-direction:column;align-items:center;grid-gap:12px;gap:12px;flex-grow:1}.music-card-container .audio-info-container .music-audio{height:40px;width:100%;max-width:500px}.music-card-container .audio-info-container .music-name{overflow:hidden;text-wrap:nowrap;text-overflow:ellipsis;color:#fff;font-family:var(--font-family-text);font-size:18px}.music-card-container .favorite-music-icon{width:30px}.music-card-container .favorite-music-icon:hover{cursor:pointer}
#album-page{display:flex;grid-gap:8px;gap:8px}#album-page #album-container{background:linear-gradient(to bottom, var(--color-background-gray-3), var(--color-background-gray-2));width:16%;border-radius:4px}#album-page #music-list-container{border-radius:4px;flex-grow:1;background-color:var(--color-background-gray-3);padding:24px;display:flex;flex-direction:column;align-items:center;grid-gap:18px;gap:18px}
#favorites-page{display:flex;flex-direction:column;align-items:center;grid-gap:24px;gap:24px;padding:24px;background:linear-gradient(to bottom, var(--color-background-gray-3), var(--color-background-gray-2));border-radius:4px;flex-grow:1}#favorites-page #no-favorites-message{color:var(--color-font-white);font-family:var(--font-family-text);font-size:28px;font-weight:bold}
#profile-page{flex-grow:1;background-color:var(--color-background-gray-3);border-radius:4px;display:flex;flex-direction:column;align-items:center;padding:38px 0}#profile-page #user-info{display:flex;flex-direction:column;background-color:var(--color-background-white);width:400px;border-radius:8px;box-shadow:0 0 8px #fff;grid-gap:18px;gap:18px;padding:34px 16px 48px 16px}#profile-page #user-info .info-container{display:flex;flex-direction:column}#profile-page #user-info .info-container .info-label{font-family:var(--font-family-label);font-size:22px}#profile-page #user-info .info-container .info{font-family:var(--font-family-text);font-weight:500;font-size:16px;border-radius:4px;display:flex;align-items:center;min-height:32px;word-break:break-all}#profile-page #user-info .image-container{align-items:center;grid-gap:18px;gap:18px}#profile-page #user-info .image-container .image-background{border:1px solid var(--color-background-gray-4);box-shadow:0 0 2px #000;border-radius:9999px;overflow:hidden;display:flex;justify-content:center;align-items:center;width:120px;height:120px}#profile-page #user-info .image-container .image-background img{width:100%}#profile-page #user-info .image-container .profile-edit-btn{text-decoration:none;background-color:var(--color-background-blue-1);color:var(--color-font-black);padding:8px 16px;border-radius:4px;font-family:var(--font-family-button);font-size:18px}
#profile-edit-page{flex-grow:1;background-color:var(--color-background-gray-3);border-radius:4px;display:flex;flex-direction:column;align-items:center;padding:38px 0}#profile-edit-page #profile-edit-form{display:flex;flex-direction:column;background-color:var(--color-background-white);width:400px;border-radius:8px;box-shadow:0 0 8px #fff;grid-gap:24px;gap:24px;padding:34px 16px 48px 16px}#profile-edit-page #profile-edit-form .image-container{align-items:center;grid-gap:18px;gap:18px}#profile-edit-page #profile-edit-form .image-container .image-background{border:1px solid var(--color-background-gray-4);box-shadow:0 0 2px #000;border-radius:9999px;overflow:hidden;width:120px;height:120px;display:flex;justify-content:center;align-items:center;cursor:pointer}#profile-edit-page #profile-edit-form .image-container .image-background img{width:100%}#profile-edit-page #profile-edit-form #info-section{display:flex;flex-direction:column;grid-gap:24px;gap:24px}#profile-edit-page #profile-edit-form .info-container{display:flex;flex-direction:column;grid-gap:4px;gap:4px}#profile-edit-page #profile-edit-form .info-container .info-label{font-family:var(--font-family-label);font-size:22px}#profile-edit-page #profile-edit-form .info-container .info{font-family:var(--font-family-text);font-weight:500;font-size:16px;border-radius:4px;display:flex;align-items:center;padding:4px;outline:none;border:1px solid var(--color-background-gray-4);transition:.5s}#profile-edit-page #profile-edit-form .info-container .info:focus{box-shadow:0 0 0 1px var(--color-background-blue-1);border-color:var(--color-background-blue-1)}#profile-edit-page #profile-edit-form .info-container textarea.info{resize:none;height:100px}#profile-edit-page #profile-edit-form .profile-edit-btn{color:var(--color-font-black);padding:12px 16px;border-radius:4px;font-family:var(--font-family-button);font-size:18px;border:none;background-color:var(--color-background-blue-1);transition:1s}#profile-edit-page #profile-edit-form .profile-edit-btn:enabled{cursor:pointer}#profile-edit-page #profile-edit-form .profile-edit-btn:disabled{filter:brightness(0.5);cursor:not-allowed}
#page-header{display:flex;flex-direction:column;padding:8px;background-color:var(--color-background-gray-3);border-radius:4px;grid-gap:18px;gap:18px}#page-header #page-info{display:flex;justify-content:space-between;align-items:center}#page-header #page-info #page-logo-container{display:flex;align-items:center;grid-gap:8px;gap:8px}#page-header #page-info #page-logo-container #page-icon-container{display:flex;justify-content:center;align-items:center;width:50px;height:50px;border-radius:9999px;background-color:var(--color-background-white)}#page-header #page-info #page-logo-container #page-icon-container img{width:80%}#page-header #page-info #page-logo-container #page-title{color:var(--color-font-white);font-family:var(--font-family-title);font-weight:bolder;font-size:24px}#page-header #page-info #user-conatiner{display:flex;align-items:center;grid-gap:12px;gap:12px;background-color:var(--color-background-gray-1);padding:8px;border-radius:9999px}#page-header #page-info #user-conatiner #user-image-container{display:flex;justify-content:center;align-items:center;background-color:var(--color-background-white);border-radius:9999px;width:40px;height:40px;overflow:hidden}#page-header #page-info #user-conatiner #user-image-container img{width:100%}#page-header #page-info #user-conatiner #user-name{font-family:var(--font-family-label);font-size:16px;color:var(--color-font-white);max-width:180px;overflow:hidden;text-wrap:nowrap;text-overflow:ellipsis}#page-header #page-nav{display:flex;justify-content:space-around;grid-gap:12px;gap:12px}#page-header #page-nav a{font-family:var(--font-family-button);font-size:20px;flex-grow:1;text-align:center;text-decoration:none;background-color:var(--color-background-gray-2);padding:8px;border-radius:4px;color:var(--color-background-white);transition:1s}#page-header #page-nav a.active{background-color:var(--color-background-gray-4);color:#000}#page-header #loading-container{width:40px;height:40px}#page-header #loading-container img{width:100%}
:root{--color-background-gray-1: rgb(48, 48, 48);--color-background-gray-2: rgb(0, 0, 0, .4);--color-background-gray-3: rgb(18, 18, 18);--color-background-gray-4: rgb(179, 179, 179);--color-background-black: rgb(0, 0, 0);--color-background-white: rgb(255, 255, 255);--color-background-green-1: rgb(30, 215, 96);--color-background-blue-1: rgb(66, 133, 244);--color-border-gray-1: rgb(167, 167, 167);--color-shadow-white: rgb(255, 255, 255);--color-font-white: rgb(255, 255, 255);--color-font-black: rgb(0, 0, 0);--font-family-title: "Poppins", sans-serif;--font-family-label: "Acme", sans-serif;--font-family-text: "Nunito", sans-serif;--font-family-input: "Akaya Telivigala", sans-serif;--font-family-button: "Acme", sans-serif}*{padding:0;margin:0;box-sizing:border-box}#root{min-height:100vh}#page-background{min-height:100vh;background-color:rgb(0, 0, 0);background-color:var(--color-background-black);display:flex;flex-direction:column;grid-gap:8px;gap:8px;padding:8px}
